.Close {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  height: 1rem;
  padding: 0.5rem;
  border-radius: 5%;
  opacity: 0.6;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  will-change: transform;
  border-radius: 50%; }
  .Close:hover, .Close:focus {
    opacity: 1;
    transform: scale(1.1); }
  @media only screen and (min-width: 800px) {
    .Close {
      top: 1rem;
      right: 1rem; } }



