.MenuItem {
  list-style: none;
  padding: 0.25rem 0.5rem;
  cursor: pointer;
  border-radius: 2px;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.2); }
  .MenuItem:hover {
    background-color: #fcf8f9;
    color: #2cd87f;
    text-shadow: initial; }
  .MenuItem:focus {
    background-color: #fcf8f9;
    color: #2cd87f;
    text-shadow: initial; }



