.Prev {
  position: absolute;
  left: 0;
  z-index: 1;
  height: 1rem;
  padding: 0.5rem;
  border-radius: 5%;
  opacity: 0.6;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  will-change: transform;
  top: 46%; }
  @media only screen and (min-width: 800px) {
    .Prev {
      left: 1rem; } }
  .Prev:hover, .Prev:focus {
    opacity: 1;
    transform: scale(1.1); }
  @media only screen and (min-width: 800px) {
    .Prev {
      top: 35%;
      padding-top: 10%;
      padding-bottom: 10%; } }



