.Container {
  margin: 0 auto;
  min-width: calc(100% - 2rem);
  width: calc(100% - 2rem); }
  @media only screen and (min-width: 800px) {
    .Container {
      min-width: calc(100% - 20%);
      width: calc(100% - 20%); } }
  @media only screen and (min-width: 1920px) {
    .Container {
      min-width: calc(100% - 40%);
      width: calc(100% - 40%); } }



